<template>
    <div>
        <div class="pageTop">
            <Menu mode="horizontal" theme="primary" :active-name="actionItem" v-on:on-select="selectMenu">
                <div>
                    <MenuItem name="console" style="height:60px;">
                        <router-link to="/" >
                            <img class="logoImg" src="../../../../assets/images/logo1.png" />
                        </router-link>
                    </MenuItem>
                </div>
                <div class="top-date-lable">
                  <p class="today_datetime">
                    <Icon type="md-calendar" style="font-size: 1.5rem"/>
                    {{today_date}} {{nowTime}}
                  </p>
                  <!--<p class="today_datetime">-->
                    <!--<Icon type="md-alarm" />-->
                    <!---->
                  <!--</p>-->
                  <!--<p>-->
                    <!--<Icon type="ios-partly-sunny" />-->
                    <!--{{today_date}}-->
                  <!--</p>-->
                </div>
                <div class="NavMenu">
                    <router-link to="/mandatory/feedback">
                      <MenuItem name="feedback">
                        <Icon type="md-paper" /> 反馈
                      </MenuItem>
                    </router-link>
                    <router-link to="/mandatory/helper">
                      <MenuItem name="help">
                        <Icon type="md-help-circle" /> 帮助
                      </MenuItem>
                    </router-link>
                    <Submenu name="user">
                        <template slot="title">
                          <Icon type="md-person" /> {{userInfo.chineseName}}
                        </template>
                          <MenuItem name="user/center">个人中心</MenuItem>
                          <MenuItem name="user/modifypassword">修改密码</MenuItem>
                          <MenuItem name="logout" v-on:click="logout">退出登录</MenuItem>
                    </Submenu>
                </div>
            </Menu>
        </div>
        <!--<div class="blank-frame-top">-->
            <!--&lt;!&ndash; 这一块的主要目的是防止页面上面60像素部分被页头遮挡 &ndash;&gt;-->
        <!--</div>-->
      <div class="pageLeft">
        <Menu mode="vertical" theme="dark" :active-name="actionItem" :accordion="accordion" :open-names="openItem" width="200" v-on:on-select="selectMenu">
          <!--<router-link to="/">-->
            <!--<MenuItem  name="console" title="工作台">-->
              <!--<Icon type="md-home" />-->
              <!--工作台-->
            <!--</MenuItem >-->
          <!--</router-link>-->
          <Submenu name="goods">
            <template slot="title">
              <Icon type="ios-shirt" />
              商品管理
            </template>
            <MenuItem name="goods/add">新增商品</MenuItem>
            <MenuItem name="goods/list">商品列表</MenuItem>
          </Submenu>
          <Submenu name="orders">
            <template slot="title">
              <Icon type="ios-paper" />
              订单管理
            </template>
            <MenuItem name="orders/list">订单管理</MenuItem>
            <MenuItem name="orders/deliver">发货管理</MenuItem>
            <MenuItem name="orders/postsale">售后管理</MenuItem>
          </Submenu>
          <Submenu name="content">
            <template slot="title">
              <Icon type="ios-book" />
              内容管理
            </template>
            <MenuItem name="content/add">新增内容</MenuItem>
            <MenuItem name="content/list">内容列表</MenuItem>
            <MenuItem name="content/draft">草稿箱</MenuItem>
            <MenuItem name="content/recycle">回收站</MenuItem>
          </Submenu>

          <Submenu name="classify">
            <template slot="title">
              <Icon type="md-list-box" />
              分类管理
            </template>
            <MenuItem name="classify/content">内容分类</MenuItem>
            <MenuItem name="classify/goods">商品分类</MenuItem>
            <MenuItem name="classify/orders">订单分类</MenuItem>
          </Submenu>
          <Submenu name="tags">
            <template slot="title">
              <Icon type="ios-pricetags" />
              标签管理
            </template>
            <MenuItem name="tags/add">新增标签</MenuItem>
            <MenuItem name="tags/list">标签列表</MenuItem>
          </Submenu>
          <Submenu name="resources">
            <template slot="title">
              <Icon type="ios-images" />
              资源管理
            </template>
            <MenuItem name="resources/images">图片管理</MenuItem>
            <MenuItem name="resources/videos">视频管理</MenuItem>
            <MenuItem name="resources/docs">文档管理</MenuItem>
          </Submenu>
          <Submenu name="users">
            <template slot="title">
              <Icon type="ios-people" />
              用户管理
            </template>
            <MenuItem name="users/add">新增用户</MenuItem>
            <MenuItem name="users/list">用户列表</MenuItem>
          </Submenu>
          <Submenu name="statistics">
            <template slot="title">
              <Icon type="md-pie" />
              统计分析
            </template>
            <MenuItem name="statistics/goods">商品统计</MenuItem>
            <MenuItem name="statistics/orders">订单统计</MenuItem>
            <MenuItem name="statistics/customer">客户统计</MenuItem>
            <MenuItem name="statistics/resources">资源统计</MenuItem>
          </Submenu>
          <Submenu name="system">
            <template slot="title">
              <Icon type="ios-construct" />
              系统管理
            </template>
            <MenuItem name="system/setting">
              基本设置
            </MenuItem>
            <MenuItem name="system/menu">
              菜单设置
            </MenuItem>
            <MenuItem name="system/api">
              接口设置
            </MenuItem>
            <MenuItem name="system/authority">
              权限设置
            </MenuItem>
            <MenuItem name="system/log">
              操作日志
            </MenuItem>
          </Submenu>
        </Menu>
      </div>
    </div>
</template>
<script>
    export default {
        name: 'pageTop',
        data () {
            return {
              accordion:true,
              today_date: '',
              nowTime: '',
              weather: '',
              openItem: [],
              actionItem: '',
              userInfo:{}
            }
        },
        mounted:function () {
          var actionItem = this.$route.fullPath;
          console.info(actionItem)
          if(actionItem){
            actionItem = actionItem.substring(1);
            this.actionItem  = actionItem;
            this.openItem = [""+actionItem.substring(0, actionItem.indexOf("/"))+""];
            console.info(actionItem)
          }
          let userInfo = sessionStorage.getItem("userInfo");
          if(userInfo){
              this.userInfo = JSON.parse(userInfo);
          }else{
            let token = sessionStorage.getItem("token");
            this.$axios.post('http://localhost:8099/admin/getUserInfo',{},{
                headers:{
                  authorization : token
                }
            })
              .then(res => {
                if (res.status === 200) {
                  // console.log("200",res)
                  if(res.data.state){
                    sessionStorage.setItem("userInfo",JSON.stringify(res.data.userInfo));
                    this.userInfo = res.data.userInfo;
                  }else{
                    console.info(JSON.stringify(res.data))
                    let fullPath = this.$route.fullPath
                    this.$router.push({//你需要接受路由的参数再跳转
                      path: "/login",
                      query: {redirect: fullPath}
                    });
                  }
                } else {
                  console.log("!=200",res)
                }
              })
              .catch(res => {
                console.log(res)
              })
          }
          var nowDate = new Date();
          this.today_date = nowDate.toLocaleDateString();
          this.nowTime = nowDate.toLocaleTimeString();
          setInterval(()=>{
            nowDate = new Date();
            this.nowTime = nowDate.toLocaleTimeString();
          },1000)
        },
        methods:{
            selectMenu:function (e) {
              this.actionItem  = e;
              //sessionStorage.setItem("actionItem",e);
              console.info(e)
              if(e=='logout'){
                this.logout(e)
              }else{
                this.$router.push({
                  //你需要接受路由的参数再跳转
                  path: "/"+e
                });
              }
            },
            logout:function (e) {
              this.$axios.post('http://localhost:8099/logout')
                .then(res => {
                  if (res.status === 200) {
                    // console.log("200",res)
                    console.info(JSON.stringify(res))
                    if(res.data.state){
                        sessionStorage.removeItem("token");
                        sessionStorage.removeItem("userInfo");
                        this.$router.push({
                          //你需要接受路由的参数再跳转
                          path: "/login"
                        });
                    }else{

                    }
                  } else {
                    console.log("!=200",res)
                  }
                })
                .catch(res => {
                  console.log(res)
                })
            }
        }
    }
</script>
<style scope>
  .pageTop{
      position: fixed;
      top: 0px;
      height: 60px;
      width: 100vw;
      z-index: 1000;
      line-height: 60px;
  }
  .NavMenu{
      float: right;
  }
  .logoImg{
      height: 60px;
      border-right: 1px solid white;
  }
  .loginModalTop{
      text-align: center;
      font-size: 1.2rem;
      font-weight: bold;
  }
  .blank-frame-top{
      height:60px;
  }
  .pageLeft {
    width: 200px;
    height: 100vh;
    position: fixed;
    left: 0px;
    top: 0px;
    padding-top:60px;
    z-index: 10;
    background-color: #515A6E;
    overflow: auto;
  }
  .leftMenu{
    width: 100%;
    height: 100%;
  }
  .ivu-menu-item,.ivu-menu-submenu{
    text-align: left;
  }

  .top-date-lable{
    float: left;
    border-radius: 20px;
    padding: 0 10px;
    height: 40px;
    line-height: 40px;
    color: white;
    font-size: 1.1rem;
    margin-top: 10px;
  }
  /*.today_datetime{*/
    /*border-right:2px solid #eeeeee ;*/
  /*}*/
</style>



