<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100vw;
  height: 100vh;
}
a{
  color: #aaa;
}
/* 设置滚动条的样式 */
::-webkit-scrollbar {
width:5px;
height: 0px;
background-color: #aaa;
}

/* 滚动槽 */
::-webkit-scrollbar-track {
  border-radius:10px;
}

/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
  border-radius:10px;
  background:black;
}
.hello{
  width: 100%;
  height: 100%;
  padding-top: 60px;
}
.container{
  padding-left: 200px;
  width:100%;
