import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/pages/index'

Vue.use(Router)

const routes = [
  { path: '*', redirect: '/404' },
  {
    path: '/',
    name: 'index',
    meta: {
      requiresAuth: true,
    },
    component: index
  }
];
importPages(require.context('../views/pages', true, /\.vue$/, 'lazy'))
function importPages (r) {
    r.keys().forEach(key => {
        if((key.split('.'))[1]=='/page'){
            routes.push({
                path: (key.split('.'))[1]+'/:id',
                meta: {
                  requiresAuth: true,
                },
                component: ()=>r(key)})
        }else if((key.split('.'))[1]=='/login'){
            routes.push({
                path: (key.split('.'))[1],
                component: ()=>r(key)})
        }else {
            routes.push({
              path: (key.split('.'))[1],
              meta: {
                requiresAuth: true,
              },
              component: ()=>r(key)})
        }

    });
}
//console.info(routes)
const router =  new Router({
  //mode: 'history',    //去除地址栏的#，但是在nginx中需要另外配置
  routes: routes
})


//  路由前置拦截器
router.beforeEach((to, from, next) => {
  /*//获取设备是否为移动端
  let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
  //当设备为移动端时
  console.info('flag',flag)
  if(flag){
    //如果请求地址是 移动版 就直接跳转，否则的话在请求地址前加上/m
    if(to.fullPath.indexOf('/m/')!=-1){
      next()
    }else{
      console.info('/m'+to.fullPath)
      next({
        path: '/m'+to.fullPath
      })
    }
  }else{
    //这里是当设备不为移动端时
    //如果请求地址是 移动版 就去掉前面的/m，否则的话就直接跳转
    if(to.fullPath.indexOf('/m/')!=-1){
      let path = to.fullPath.replace('/m/','/')
      console.info(path)
      next({
        path: path
      })
    }else{
      next()
    }
  }*/
  // 判断是否需要登录权限
  if (to.matched.some(res => res.meta.requiresAuth)) {
      // 判断是否登录
    console.info(sessionStorage.getItem('token'))
      if (sessionStorage.getItem('token')) {
          next()
      } else {// 没登录则跳转到登录界面
        next({
          path: '/login',
          query: {redirect: to.fullPath}
        })
      }
  }else{
    next();
  }
})


export default router



