import PageFooter from './index.vue'

const pageFooter = {
    install:function(Vue){
        // Vue.component()是用来注册全局组件的
        Vue.component('pageFooter',PageFooter)
    },  // loading'这就是后面可以使用的组件的名字，install是vue实例上默认的一个方法
}

export default pageFooter


