// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import axios from 'axios'
import qs from 'qs'   //post请求传参
import router from './router'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import iview from 'iview'
import store from 'store'
import 'iview/dist/styles/iview.css'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import pageTop from '@/views/components/inc/_top'
import pageFooter from '@/views/components/inc/_footer'

/**
 * 默认10S请求超时
 */
axios.defaults.timeout = 10000;
/**
 * 设置post请求头
 */
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';

Vue.prototype.$axios = axios
Vue.prototype.$qs = qs
Vue.prototype.$store = store
//Vue.prototype.$router = router

Vue.config.productionTip = false
Vue.use(iview)
Vue.use(MintUI)
Vue.use(pageTop)
Vue.use(pageFooter)
Vue.use(mavonEditor)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App,
    'pageTop':pageTop,
    'pageFooter': pageFooter,
  },
  template: '<App/>'
})



/**
 * 请求拦截器
 */
axios.interceptors.request.use(
  config => {
      // 每次发送请求之前判断是否存在token，如果存在，则统一在http请求的header都加上token，不用每次请求都手动添加了
      // 即使本地存在token，也有可能token是过期的，所以在响应拦截器中要对返回状态进行判断
      let token = sessionStorage.getItem("token");
      console.info("拿到的TOKEN",token);
      if(token) {
          config.headers.authorization = token
      }
      return config;
  },
  error => {
      console.info("拦截器错误信息：",error)
      return Promise.error(error);
  }
)

// 响应拦截器
axios.interceptors.response.use(
  response => {
      if (response.status === 200) {
          if(response.headers.authorization){
              sessionStorage.setItem("token", response.headers.authorization)
          }
          return Promise.resolve(response);
      } else {
          return Promise.reject(response);
      }
  },
  // 服务器状态码不是200的情况
  error => {
      if (error.response) {
          console.info(error)
          switch (error.response.status) {
              // 401: 未登录
              // 未登录则跳转登录页面，并携带当前页面的路径
              // 在登录成功后返回当前页面，这一步需要在登录页操作。
              case 401:
                  router.replace({
                      path: '/login',
                      query: {
                          redirect: router.currentRoute.fullPath
                      }
                  });
                  break;
              // 403 token过期
              // 登录过期对用户进行提示
              // 清除本地token和清空vuex中token对象
              // 跳转登录页面
              case 403:
                  // 清除token
                  localStorage.removeItem('token');
                  store.commit('loginSuccess', null);
                  // 跳转登录页面，并将要浏览的页面fullPath传过去，登录成功后跳转需要访问的页面
                  setTimeout(() => {
                      router.replace({
                          path: '/login',
                          query: {
                              redirect: router.currentRoute.fullPath
                          }
                      });
                  }, 1000);
                  break;
              // 404请求不存在
              case 404:
                  iview.Message.error('网络请求不存在')
                  break;
              // 其他错误，直接抛出错误提示
              default:
                  iview.Message.error(error.response.data.message)
                  break;
          }
          return Promise.reject(error.response);
      }
  }
)



