<template>
    <div>
        <div class="blank-frame">
            <!-- 这一块的主要目的是防止页面下面300像素部分被页脚遮挡 -->
        </div>
        <div class="footer">
            <p>
                <router-link to="/mandatory/about" >关于我们</router-link>　|　
                <router-link to="/mandatory/contact">联系我们</router-link>　|　
                <router-link to="/mandatory/statement">法律声明</router-link>
            </p>
            <p>
                Copyright © {{year}} {{Corporate}} All Rights Reserved. 
            </p>
            <p v-on:click="clickThisP">
                {{ICP}}  
            </p>
        </div>
    </div>
</template>
<script>
export default {
    name: 'pageFooter',
    data () {
        return {
            year: '',
            Corporate: '',
            ICP:''
        }
    },
    //用于数据初始化
    created() {
        this.year = new Date().getFullYear();
        this.Corporate = 'zolysoft.com';
        this.ICP = '沪ICP备17054651号-1';
    },
    methods: {
        clickThisP:function(e){
          console.info("你点击了ICP备案信息")  
          this.$Message.info('你点击了ICP备案信息')
        }
    }
}
</script>

<style scope>
.blank-frame{
    height: 100px;
    width: 100%;
}
.footer{
    background-color: beige;
    padding-top: 30px;
    height: 100px;
    width: 100%;
    position: fixed;
    bottom: 0px;
    text-align: center;
    color:#999;
    z-index: 1000;
}
</style>



